import { Flex, HStack, Text, VStack } from "@chakra-ui/react";
import React, { useMemo } from "react";

function FirstLayer() {
  const dateAndTime = useMemo(() => {
    const currentDate = new Date();
    const year = currentDate.getFullYear();
    const month = String(currentDate.getMonth() + 1).padStart(2, "0");
    const day = String(currentDate.getDate()).padStart(2, "0");

    const formattedDate = `${year}.${month}.${day}`;

    const hours = String(currentDate.getHours()).padStart(2, "0");
    const minutes = String(currentDate.getMinutes()).padStart(2, "0");
    const seconds = String(currentDate.getSeconds()).padStart(2, "0");

    const formattedTime = `${hours}:${minutes}:${seconds}`;

    return { formattedDate, formattedTime };
  }, []);
  const { formattedDate, formattedTime } = dateAndTime;
  return (
    <HStack w={"100%"} h={"10%"} p={1}>
      <Flex
        bgColor={"white"}
        h={"100%"}
        alignItems={"center"}
        fontWeight={"bold"}
        p={4}
        fontSize={'large'}
        flex={1}
      >
        Kingstar v1.1
      </Flex>
      <Flex
        bgColor={"blue.600"}
        color={"white"}
        fontWeight={"bold"}
        p={4}
        w={"10%"}
      >
        0 Round
      </Flex>
      <Flex w={"10%"} bgColor={"white"} fontWeight={"bold"} p={4}>
        General
      </Flex>
      <Flex w={"10%"} bgColor={"white"} fontWeight={"bold"} p={4}>
        0
      </Flex>
      <Flex
        bgColor={"black"}
        w={"10%"}
        justifyItems={"center"}
        alignItems={"center"}
        fontWeight={"bold"}
      >
        <VStack justifyItems={"center"} alignItems={"center"} w={"100%"} p={4}>
          <Text color={"yellow"}>On {formattedDate}</Text>
          <Text color={"yellow"} textAlign={"center"}>
            {formattedTime}
          </Text>
        </VStack>
      </Flex>
      <Flex bgColor={"white"} w={"10%"} fontWeight={"bold"} p={4}>
        BET_1
      </Flex>
    </HStack>
  );
}

export default FirstLayer;
